.title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 15px;
  padding: 0px 15px;
  margin: 15px 0px;
  b {
    color: #797c8c;
    font-size: 18px;
    text-transform: capitalize;
  }
  button {
    background-color: #4eac6e;
    color: white;
    border: none;
    padding: 3px;
    border-radius: 4px;
    transition: all 0.2s ease-in-out;
    i {
      font-size: 16px;
    }
  }
}
.user_card {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  padding: 10px 15px;
  transition: background-color 0.2s ease-in-out;
  cursor: pointer;
  &:is(.active) {
    background-color: #4eac6e;
    .user {
      .msg {
        span {
          color: white;
        }
      }
      .msg[typing] {
        &::after {
          color: white;
        }
      }
    }
    .time {
      label {
        background-color: #7bbf90;
        color: white;
      }
      span {
        color: white;
      }
    }
  }
  .user {
    display: flex;
    align-items: center;
    gap: 10px;
    .msg {
      display: flex;
      flex-direction: column;
      span {
        color: var(--text-clr);
        font-size: 14px;
        &:last-child {
          font-size: 12px;
        }
      }
    }
    .msg[typing] {
      &::after {
        content: attr(typing);
        font-size: 12px;
        color: #4eac6e;
        display: -webkit-box;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
        overflow: hidden;
      }
      span {
        &:last-child {
          display: none;
        }
      }
    }
  }
  .time {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    gap: 2px;
    min-width: fit-content;
    &:is(.top) {
      align-self: flex-start;
    }
    label {
      display: flex;
      align-items: center;
      justify-content: center;
      align-self: flex-end;
      font-size: 10px;
      margin: 0px;
      background-color: #4eac6e;
      color: white;
      min-width: 18px;
      width: fit-content;
      padding: 0px 5px;
      height: 20px;
      border-radius: 4px;
      font-weight: bold;
      cursor: inherit;
    }
    span {
      color: var(--text-clr);
      font-size: 12px;
    }
  }
}
